@import "./variable";

.nav {
  background: #e0ffdb;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 6rem 0.5rem;
  a {
    text-decoration: none;
    color: $title_green;
  }
  .logo {
    display: flex;
    align-items: center;
    img {
      width: 3.2rem;
      height: 3.2rem;
      margin-right: 1.6rem;
    }
    .logo_name {
      font-weight: 700;
      font-size: 1.4rem;
      letter-spacing: 0.075em;
      color: $title_green;
    }
  }
  .profile {
    .name {
      display: flex;
      align-items: center;
      font-weight: 400;
      font-size: 1.25rem;
      letter-spacing: 0.16rem;
    }
  }
}

@media all and (max-width: 1300px) {
  .nav {
    padding: 0.8rem 3.6rem 0.5rem;
    .logo {
      img {
        width: 2.9rem;
        height: 2.9rem;
        margin-right: 1.25rem;
      }
      .logo_name {
        font-size: 1.18rem;
      }
    }
    .profile {
      .name {
        font-size: 1.1rem;
      }
      button {
        scale: (0.8);
        margin-left: -0.6rem;
      }
    }
  }
}

@media all and (max-width: 900px) {
  .nav {
    padding: 0.55rem 2.4rem 0.3rem;
    .logo {
      img {
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1.2rem;
      }
      .logo_name {
        font-size: 1.1rem;
      }
    }
    .profile {
      .name {
        font-size: 1rem;
      }
      button {
        scale: (0.7);
        margin-left: -1.2rem;
      }
    }
  }
}
